@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

body {
  margin:0; 
  padding:0; 
  font-family: 'Raleway', sans-serif;
}
a, a:hover, a:visited {
  color: #0071bc;
}
.mapboxgl-map {
  top:0; 
  height: 100vh;
  width:100vw;
  position: fixed;
  z-index: -5;
}
.mapboxgl-ctrl-minimap {
  right: 10px;
  top: calc(100vh - 200px);
}
/* remove mapbox logo */
/* .mapboxgl-ctrl-bottom-left {
  display: none;
} */

.map-interactive {
  position: fixed;
  bottom: 10px;
  right: 10px;
  background-color: rgba(240, 240, 240, 0.432);
  border-radius: 50%;
  z-index: 10000 !important;
}

.map-interactive-icon {
  margin-top: 8px;
}
#story {
  position: relative;
}
#header {
  margin: 3vh auto;
  width: 90vw;
  padding: 2vh;
  text-align: center;
}
#footer {
  width: 100%;
  min-height: 5vh;
  padding-top: 2vh;
  padding-bottom: 2vh;
  text-align: center;
  line-height: 25px;
  font-size: 17px;
}
#features {
  padding-top: 10vh;
  padding-bottom: 10vh;
  z-index: 100;
}
.centered {
  width: 50vw;
  margin: 0 auto;
}
.lefty {
  width: 33vw;
  margin-left: 5vw;
}
.righty {
  width: 33vw;
  margin-left: 62vw;
}
.light {
  color: #444;
  background-color: #fafafa;
}
.dark {
  color: #fafafa;
  background-color: #444;
}
.step {
  padding-bottom: 70vh;
  /* margin-bottom: 10vh; */
  opacity: 0.25;
}
.step.active {
  opacity: 0.95;
}

.step div {
  padding:  25px 50px;
  line-height: 25px;
  font-size: 13px;
}

.step img {
  width: 100%;
}

#tooltip {
  background-color: black;
  opacity: 0.8;
  color: gainsboro;
  font-size: small;
}

@media (max-width: 750px) {
  #features {
      width: 90vw;
      margin: 0 auto;
  }
  .mapboxgl-ctrl-minimap {
    display: none;
  }
}

.flourish-embed {
  width: 100%;
  padding: 0px !important;
  transition: transform .2s; /* Animation */
}

/* .flourish-embed:hover {
  transform: scale(1.3); 
}
*/

.flourish-credit {
  display: none !important;
}

.legend {
  padding: 0px !important;
}

.legend-signature {
  padding: 0px !important;
  margin-right: 10px;
}